  }
  .blue_btn,
  .gray_btn span {
    transform: translateY(-1px);
  }
  /*----Buttons----*/
  .blur {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.768);
    bottom: 0;
    z-index: 99;
  }
  
  /*----Buttons----*/
  
  /*-----Hovers-----*/
  .hover1:hover {
    background: var(--bg-secondary);
  }
  .hover2:hover {
    background: var(--bg-third);
  }
  .hover3:hover {
    background: #d4d6da3a;
  }
  /*-----Hovers-----*/
  
  .card {
    height: 500px;
    width: 500px;
    background: red;
  }
  
  /*----Scrollbar----*/
  
  .scrollbar::-webkit-scrollbar {
    width: 8px;
  }
  .scrollbar::-webkit-scrollbar-thumb {
    background: #aeaeafa1;
    border-radius: 5px;
  }
  /*----Scrollbar----*/
  
  /*---Circles------*/
  .small_circle {
    position: relative;
    height: 55px;
    width: 36px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 11px;
    /* background: var(--bg-third); */
    cursor: pointer;
  }
  .small_circle1 {
    position: relative;
    height: 55px;
    width: 36px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 11px;
    /* background: var(--bg-third); */
    cursor: pointer;
  }
  /*---Circles------*/
  
  /*----filters---*/
  .filter_blue {
    filter: invert(39%) sepia(57%) saturate(200%) saturate(200%) saturate(200%)
      saturate(200%) saturate(200%) saturate(147.75%) hue-rotate(202deg)
      brightness(97%) contrast(96%);
  }
